/*以下追加*/
*, *::before, *::after {
  box-sizing: border-box;
}

ul, ol, li {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

dl, dd {
  margin: 0;
}

figure {
  margin: 0;
}

.c-bdc--green {
  border: 1px solid;
  border-color: green;
}

.c-bdc--gray {
  border: 1px solid;
  border-color: #dfdfdf;
}

.c-bg--green {
  background-color: #75ce75;
}

.c-bg--red {
  background-color: red;
}

.c-bg--gray {
  background-color: #f0efef;
}

.c-bg--beige {
  background-color: #ffffd4;
}

.c-bg--white {
  background-color: white;
}

.c-grid {
  display: grid;
  justify-content: center;
  padding: 0 5%;
  gap: 25px;
  overflow-x: hidden;
  grid-template: "header" auto "nav" auto "intro" auto "news" auto "list" auto "footer" 150px/minmax(50%, 1000px);
}
.c-grid--header {
  grid-area: header;
}
.c-grid--nav {
  grid-area: nav;
}
.c-grid--introduce {
  grid-area: intro;
}
.c-grid--news {
  grid-area: news;
}
.c-grid--dininglist {
  grid-area: list;
}
.c-grid--footer {
  grid-area: footer;
}

.c-head {
  text-align: center;
}

.c-image {
  vertical-align: bottom;
  max-width: 100%;
  width: 100%;
}
.c-image--main {
  vertical-align: bottom;
  width: 100%;
}

.c-oval {
  border-radius: 25%;
}

.c-square {
  border-radius: 10px;
}

.c-text {
  font-size: min(2.8vw, 16px);
  line-height: 1.5;
}
.c-text--small {
  font-size: min(2.5vw, 14px);
}
.c-text--large {
  font-size: min(3vw, 20px);
}
.c-text--underline {
  text-decoration: underline;
  text-decoration-color: black;
}

.c-text--bold {
  font-weight: 700;
}

.p-cards {
  margin-bottom: 10px;
}
.p-cards__image {
  width: 100%;
}
.p-cards__contents {
  display: flex;
  flex-wrap: wrap;
  padding: 3%;
}
.p-cards__contents > dt {
  width: 30%;
  padding-bottom: 5px;
}
.p-cards__contents > dd {
  width: 70%;
}
.p-cards__comment {
  padding: 8%;
  background-color: #ffffd4;
  margin: 15px 0;
}
.p-cards__link {
  display: flex;
  justify-content: space-around;
  margin: 5px 0;
}

.p-dininglist {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 10px;
}

.p-footer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 120%;
  margin-left: -10%;
}

.p-header {
  display: flex;
  justify-content: center;
}

.p-heading {
  display: inline-block;
  width: 100%;
}

.p-introduce {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(330px, 1fr));
  gap: 10px;
}
.p-introduce__head {
  font-size: min(2.8vw, 16px);
}
.p-introduce__image {
  width: 100%;
}
.p-introduce__description {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
.p-introduce__description > p {
  display: block;
}

.p-label {
  text-align: center;
  width: 100%;
  border-radius: 20px;
  height: 40px;
  line-height: 40px;
  background-color: #75ce75;
}

.p-nav__list {
  display: flex;
  justify-content: center;
}

.p-news {
  padding: 5%;
}

.p-question {
  padding: 5%;
  width: 100%;
}

.u-image-add {
  border-radius: 10px 10px 0 0;
}